import React, { useState, useEffect, useRef } from "react";
import Container from "./container";
import "./style.css";
function Menu() {
  const [allMoney, setAllMoney] = useState(0);
  const checker = useRef();
  const [govermentPer, setGovermentPer] = useState();
  const [govermentMon, setGovermentMon] = useState();
  const [ownPer, setOwnPer] = useState();
  const [ownMon, setOwnMon] = useState();
  const inputChange = (event) => {
    const { name, value } = event.target;
    if (name === "allMoney" && (parseInt(value) > 0 || value === "")) {
      setAllMoney(value);
    }
    if (parseInt(allMoney) > 0 || allMoney === "") {
      if (parseInt(value) <= 100 || value === "") {
        if (name === "govermentPer") {
          setGovermentPer(value);
          checker.current = 1;
        }
        if (name === "ownPer") {
          setOwnPer(value);
          checker.current = 3;
        }
      }
      if ((parseInt(value)<=parseInt(allMoney)&&parseInt(value)>0)||value===""){
        if (name === "govermentMon") {
          setGovermentMon(value);
          checker.current = 2;
        }
        if (name === "ownMon") {
          setOwnMon(value);
          checker.current = 4;
        }
      }
    }
  };
  useEffect(() => {
      setGovermentPer("");
      setGovermentMon("");
      setOwnPer("");
      setOwnMon("");
      checker.current=0;
  }, [allMoney]);
  useEffect(() => {
    if (checker.current === 1) {
      let bool = parseInt(govermentPer) > 0 && parseInt(govermentPer) <= 100;
      if (bool && allMoney !== "") {
        setGovermentMon(Math.round((parseFloat(govermentPer) / 100) * parseInt(allMoney)));
        setOwnPer(100 - parseFloat(govermentPer));
        setOwnMon(Math.round(parseInt(allMoney) * (parseInt(ownPer) / 100)));
      } else {
        setGovermentMon("");
      }
    }
    if (checker.current === 2) {
      let X = parseInt(govermentMon) / parseInt(allMoney);
      setGovermentPer(isNaN(X) ? govermentPer : Math.round(X * 100));
      setOwnPer(100-Math.round(govermentPer))
      setOwnMon(parseInt(allMoney) - parseInt(govermentMon));
    }
    if (checker.current === 3) {
      setGovermentPer(100 - parseInt(ownPer));
      let bool = parseInt(ownPer) > 0 && parseInt(ownPer) <= 100;
      if (bool && allMoney !== "") {
        setOwnMon(parseInt(allMoney) * (parseInt(ownPer) / 100));
        setGovermentMon(parseInt(allMoney)-Math.round(ownMon))
        setGovermentPer(100 - parseInt(ownPer));
      } else {
        setOwnMon("");
      }
    }
    if (checker.current === 4) {
      let X = parseInt(ownMon) / parseInt(allMoney);
      setOwnPer(isNaN(X) ? ownPer : Math.round(X * 100));
      setGovermentPer(100-parseInt(ownPer));
      setGovermentMon(parseInt(allMoney) - parseInt(ownMon));
    }
  });
  return (
    <div>
      <Container
        allMoney={allMoney}
        setAllMoney={setAllMoney}
        govermentPer={govermentPer}
        setGovermentPer={setGovermentPer}
        govermentMon={govermentMon}
        setGovermentMon={govermentMon}
        ownPer={ownPer}
        ownMon={ownMon}
        inputChange={inputChange}
      />
    </div>
  );
}
export default Menu;
