function Container(props) {
  return (
    <div className="container">
      <div className="elements">
        <div>
          <h2>სრული თანხა</h2>
          <input
            type="text"
            name="allMoney"
            placeholder="სრული თანხა"
            onChange={props.inputChange}
            value={props.allMoney !== 0 ? props.allMoney : ""}
          />
        </div>
        <div>
          <h2>სახელმწიფო დაფინანსების პროცენტი</h2>
          <input
            type="text"
            name="govermentPer"
            onChange={props.inputChange}
            value={(props.govermentPer !== 0&&!isNaN(props.govermentPer)) ? props.govermentPer: ""}
            placeholder="სახელმწიფო დაფინანსება %"
          />
        </div>
        <div>
          <h2>სახელმწიფო დაფინანსების თანხა</h2>
          <input
            type="text"
            name="govermentMon"
            onChange={props.inputChange}
            value={(props.govermentMon !== 0&&!isNaN(props.govermentMon)) ? props.govermentMon : ""}
            placeholder="სახელმწიფო დაფინანსების $"
          />
        </div>
        <div>
          <h2>თქვენგან დასამატებელი პროცენტი</h2>
          <input
            type="text"
            name="ownPer"
            value={(props.ownPer === 0||isNaN(props.ownPer))?"":props.ownPer}
            placeholder="თქვენგან დასამატებელი %"
            onChange={props.inputChange}
          />
        </div>
        <div>
          <h2>თქვენგან დასამატებელი თანხა</h2>
          <input
            type="text"
            name="ownMon"
            value={(props.ownMon === 0||isNaN(props.ownMon)) ? "" : props.ownMon}
            placeholder="თქვენგან დასამატებელი $"
            onChange={props.inputChange}
          />
        </div>
        <div>
          <button onClick={() => console.log("clicked")}>Send</button>
        </div>
      </div>
    </div>
  );
}

export default Container;
